.Header {
    width: 100%;
    height: 10vh;
    background-color: #a69cac;        
    font-size: xx-large;     
    text-transform: uppercase;
    /* text-align: center;   */
    display: flex;    
    align-items: center;
    justify-content: flex-start;
    align-content: center;
    position: sticky;
    border-radius: 0px 0px 5px 5px;    
    top: 0;
    z-index: 1;   

    .Hov{
        text-decoration: none;
        color: rgb(0, 0, 0);      
        margin-left: 100px; 
        background-color: #474973;
        border-radius: 10px 10px 10px 10px;
        padding: 10px;
    }

    .Hov:hover{
        background-color:rgba(163, 157, 223, 0.739);
        color: white;
    }
}

.img{
    width: 30px;
    height: 20px;    
}

a{    
    text-decoration: none;
    margin: 50px;
}